@import './Global.scss';


/* #region ==================== BASE ==================== */

.employerContainer * {
    // border: 2px solid #00ffff;

    text-align: left;
    line-height: 1.25em;

    margin: 0;
    // padding: 0;
    padding: 0 0 0.125em 0;
}

.employerContainer {
    // border: 2px solid #ff0000;

    // letter-spacing: 0.0375em;

    width: $body-width;
    min-width: $body-min-width;
    max-width: $body-max-width;

    // margin: 0 auto;
    margin: 0 auto 120px auto;

    
    .employerList {
        // border: 2px solid #00ff00;

        text-align: left;

        margin: 0 auto;
        padding: 125px 0 0 0;

        .employerItem {
            // border: 1px solid #0000ff;

            width: 100%;
            height: auto;

            margin: 0;
            // padding: 0 0 50px 0;
            padding: 0 0 2.5em 0;

            position: relative;
            display: inline-block;

            .logo {
                // border: 2px solid #0000ff;
                background: none;

                width: 12.5%;

                float: left;

                // margin: inherit;
                // margin: 1.25% auto 0 auto;
                // padding: 0;

                margin: 5px 0 0 0;
                padding: 0;

                position: absolute;
                top: 0;
            }

            .employerInfo {
                // border: 2px solid #ff0000;

                font-size: 1em;
                // font-weight: bold;
                // line-height: 1em;
                letter-spacing: 0.05em;

                width: 80%;
                height: 100%;

                // margin: inherit;
                // padding: 0;

                float: right;

                h1 {
                    // border: 2px solid #ffff00;

                    font-size: 1.5em;
                    // font-weight: bold;
                    // text-align: inherit;

                    // margin: inherit;
                    // padding: inherit;
                }

                h2 {
                    // border: 2px solid #ff00ff;

                    font-size: 1.125em;
                    // font-weight: bold;
                    // text-align: inherit;

                    width: 60%;

                    // margin: inherit;
                    // padding: inherit;
                    // padding: 0 0 10px 0;

                    display: inline-block;
                }

                .employerDates {
                    // border: 2px solid #00ffff;

                    // font-size: 1em;
                    // font-weight: bold;
                    text-align: right;
                    // letter-spacing: 0.025em;

                    width: 22.5%;

                    // margin: inherit;
                    // padding: inherit;

                    // display: inline-block;
                    float: right;
                }

                ul {
                    // margin: inherit;
                    // padding: 15px 0;

                    li {
                        // font-weight: bold;

                        letter-spacing: 0.05em;

                        padding-left: 0.625em;
                        text-indent: -0.625em;

                        list-style: none;
                    }
                }

                p {
                    // font-weight: bold;
                    // letter-spacing: 0.05em;

                    // margin: inherit;
                    padding: 0.5em 0 0 0;
                }
            }
        }
    }

    .skillHed {
        // border: 2px solid #ff0000;
        // border-top: 1px solid #999999;
        // border-bottom: 1px solid #999999;

        color: #666666;
        font-size: 1.75em;
        // letter-spacing: 0.025em;

        width: 100%;

        margin: 0.75em 0;
        // margin: 0 0 0.75em 0;
        // padding: 0.0625em 0 0 0;
    }

    .skillCatHed {
        // border: 2px solid #ff0000;
        border-bottom: 1px solid #999999;

        color: #666666;
        font-size: 1.25em;
        // letter-spacing: 0.05em;

        width: 100%;

        margin: 0.75em 0;
        // margin: 0.75em 0 0.5em 0;
    }

    .skillList {
        // border: 2px solid #ff0000;

        font-weight: 600;
        // letter-spacing: 0.05em;

        // margin: 0;
        // padding: 0 0 100px 0;

        display: grid;
        // grid-template-columns: repeat(2, auto);
        grid-template-columns: 48.75% 48.75%;
        column-gap: 2.5%;
        align-items: center;

        .skillItem {
            // border: 2px solid #00ff00;

            margin: 0;
            padding: 0 0 10px 0;

            .skillBar {
                // border: 2px solid #0000ff;

                // background: #999999;
                // @include bgGradient04();

                // fill: #ff00ff;
                // fill: none;
                // @include fillGradient01();

                .skillBarRect01, .skillBarRect02 {
                    // border: 2px solid #ff00ff;

                    // fill: #cccccc;
                    // @include fillGradient01();

                    // stroke: #ff00ff;

                    // width: 100%;
                    // height: 100%;

                    margin: 0;
                    padding: 0;
                }
            }
        }
    }
}

/* #endregion ==================== BASE ==================== */



