                // border: 2px solid #00ff00;

                margin: 0 0 0 5vw;
                // padding: 80px 0;

                .employerItem {
                    // border: 1px solid #ff0000;

                    .employerInfo {
                        // border: 1px solid #00ff00;

                        h1, h2 {
                            letter-spacing: 0.005em;
                        }

                        h2 {
                            // border: 1px solid #0000ff;
                            width: 80%;
                        }

                        .employerDates {
                            // border: 1px solid #ff00ff;
