/* #region ========================= MOBILE: 375px ========================= */

@media  
    // only screen and (min-device-width: 375px) 
    // only screen and (min-device-width: 375px), 
    only screen and (max-device-width: 767px) 
    { 
        .employerContainer {
            // border: 2px solid #ff00ff;

            width: 90vw;
            min-width: 50vw;
            max-width: 100vw;

            .employerList {
                // border: 2px solid #00ffff;

                padding: 110px 0;
                margin: 0;

                .employerItem {
                    // border: 1px solid #ff00ff;

                    padding: 0 0 1em 0;

                    position: relative;

                    .logo {
                        // border: 2px solid #ff0000;

                        width: 18.75%;

                        margin: 1.25% auto 0 auto;
                    }

                    .employerInfo {
                        // border: 2px solid #ff0000;

                        width: 76.25%;

                        h1, h2 {
                            letter-spacing: 0.005em;
                            width: auto;
                        }

                        h1 {
                            font-size: 1.375em;
                        }

                        h2 {
                            font-size: 1.0625em;
                        }

                        .employerDates {
                            text-align: left;

                            width: auto;

                            padding: 0.25em 0;

                            float: unset;
                        }

                        p {
                            font-size: 0.9375em;
                            line-height: 1.5em;
                            padding: 0.25em 0 0 0;
                        }
                    }
                }
            }
        }
    }

/* #endregion ========================= MOBILE: 375px ========================= */
